// src/components/Sidebar.tsx
"use client";

import Link from "next/link";
import React from "react";

export default function Sidebar() {
  const menu = [
    { href: "/dashboard", label: "Dashboard" },
    { href: "/employees", label: "Employees" },
    { href: "/shifts", label: "Manage Shifts" },
    { href: "/leaves", label: "Leaves & Holidays" },
    { href: "/approvals", label: "Approval Requests" },
    { href: "/payroll", label: "Payroll" },
    { href: "/loans", label: "Loans & Advances" },
    { href: "/reports", label: "Reports" },
    { href: "/users", label: "User Management" },
    { href: "/logs", label: "Activity Logs" },
    { href: "/config", label: "Configuration" },
  ];

  return (
    <aside className="w-64 bg-white border-r min-h-screen p-4">
      <div className="mb-6">
        <div className="text-xl font-bold">My App</div>
      </div>

      <nav className="flex flex-col gap-2">
        {menu.map((m) => (
          <Link key={m.href} href={m.href} className="py-2 px-3 rounded hover:bg-gray-100">
            {m.label}
          </Link>
        ))}
      </nav>
    </aside>
  );
}
